
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import Vue from 'vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'

Vue.use(AlertPlugin);
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

Vue.use(ConfirmPlugin);
const wx = require('weixin-js-sdk')

var showWxJsSdkLoading = true;
var showUploadPictureLoading =false;
var uploadBannerButtonLabel = '';
var uploadAuctionButtonLabel = '';
var uploadShowLabel = '0';
var uploadDescLabel = '0';

var goods = {
  bannerPictureWxServerId:'',
  title:'',
  startingPrice:0,
  bidIncrement:0,
  cashDeposit:0,
  delayCycle:0,
  startTime:'',
  endTime:'',
  auctionPictureWxServerId:'',
  showPicturesWxServerId:[],
  descPicturesWxServerId:[]
};

//TODO 
//
var serverIds = [];
var choosePictureIds = [];
var currentUploadPictureIndex = 0;
function uploadChoosePictures(){
  var choosePictureId = choosePictureIds[currentUploadPictureIndex];
  wx.uploadImage({
      localId: choosePictureId, // IDchooseImage
      isShowProgressTips: 1, // 1
      success: function (res) {
          serverIds.push(res.serverId);
          currentUploadPictureIndex++;
          if(currentUploadPictureIndex >= choosePictureIds.length){
            showUploadPictureLoading = false;
            savePicture();
          }else{
            uploadChoosePictures();
          }
      }
  });
}

//ID
var globalPictureUsage = "";
function savePicture(){
  if("banner" == globalPictureUsage){
    goods.bannerPictureWxServerId = serverIds[0];
    uploadBannerButtonLabel = '';
  }

  if("auction" == globalPictureUsage){
    goods.auctionPictureWxServerId = serverIds[0];
    uploadAuctionButtonLabel = '';
  }

  if("show" == globalPictureUsage){
    goods.showPicturesWxServerId = goods.showPicturesWxServerId.concat(serverIds);
    uploadShowLabel = '' + goods.showPicturesWxServerId.length + '';
  }

  if("desc" == globalPictureUsage){
    goods.descPicturesWxServerId = goods.descPicturesWxServerId.concat(serverIds);
    uploadDescLabel = '' + goods.descPicturesWxServerId.length + '';
  }
}

var AddGoodsComponent = {
  components: {
    Box,
    XInput,
    XButton,
    Divider,
    Flexbox,
    FlexboxItem,
    Datetime,
    Confirm,
    Loading,
    Alert
  },
  mounted:function(){
    this.$http.get('/wx/jssdk/config').then(
      function(response){
        wx.config({
          appId : response.body.appId,
          timestamp : response.body.timestamp,
          nonceStr : response.body.nonceStr,
          signature : response.body.signature,
          jsApiList : ["chooseImage","uploadImage"]
        });
        wx.ready(this.wxJsSdkInitialized);
        wx.error(function(res){
          this.$vux.alert.show({
            title: '',
            content: '',
            onHide () {
              window.history.back();
            }
          });
        })
      }
      ,
      function(){
        this.$vux.alert.show({
          title: '',
          content: '',
          onHide () {
            window.history.back();
          }
        });
      });
  },
  data(){
    return{
      goods:goods,
      uploadBannerButtonLabel:uploadBannerButtonLabel,
      uploadAuctionButtonLabel:uploadAuctionButtonLabel,
      uploadShowLabel:uploadShowLabel,
      uploadDescLabel:uploadDescLabel,
      showSubmitConfirm:false,
      showSubmitLoading:false,
      showSubmitSuccessTip:false,
      showSubmitFailTip:false,
      showWxJsSdkLoading:showWxJsSdkLoading,
      showUploadPictureLoading:showUploadPictureLoading
    }
  },
  methods:{
    wxJsSdkInitialized(){
      this.showWxJsSdkLoading =false;
    },
    uploadPicture(count,pictureUsage){
      globalPictureUsage = pictureUsage;
      wx.chooseImage({
        count: count, // 9
        //sizeType: ['original', 'compressed'], // 
        //sourceType: ['album', 'camera'], // 
        success: function (res) {
          currentUploadPictureIndex = 0;
          choosePictureIds = res.localIds; // IDlocalIdimgsrc
          serverIds = [];
          showUploadPictureLoading = true;
          uploadChoosePictures();
        }
      });
    },
    setNow (timeToSet) {
      timeToSet = dateFormat(new Date(), 'YYYY-MM-DD HH:mm:ss');
    },
    submitGoods(){
      this.showSubmitLoading = true;
      this.$http.post("/goods",goods).then(
        function(response){
          this.showSubmitLoading = false;
          this.$vux.confirm.show({
            title : '',
            content : '',
            confirmText : "",
            cancelText : "",
            onCancel () {
              window.history.back();
            },
            onConfirm () {
              window.location.reload();
            }
          });
        },
        function(){
          this.showSubmitLoading = false;
          this.$vux.alert.show({
            title: '',
            content: ''
          });
        }
      )
    },
    backToHome(){
      window.history.back();
    }
  }
};

export default AddGoodsComponent;



