
import FormPreview from 'vux/src/components/form-preview/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'

import Vue from 'vue'
Vue.use(AlertPlugin)

var test = "test";

export default{
  components: {
    FormPreview
  },
  data(){
    return{
      orderList:[
        {
          id:123,
          price:"2400.00",
          fields:[
            {
              label: '',
              value: '1485419079408'
            },
            {
              label: '',
              value: ''
            },
            {
              label: '',
              value: ''
            }
          ],
          status:"WAIT_FOR_PAY"
        },
        {
          id:456,
          price:"2300.00",
          fields:[
            {
              label: '',
              value: '1485419079408'
            },
            {
              label: '',
              value: ''
            }, {
              label: '',
              value: ''
            }
          ],
          status:"PAY_SUCCESS"
        }
      ]
    }
  },
  mounted:function(){
    var orderList = this.orderList;
    var vux = this.$vux;
    var httpClient = this.$http;
    this.$http.get('/orders/all').then(
      function(response){
        if("0" == response.resultCode){
          response.items.forEach(function(item){
            var goodsTitle;
            httpClient.get("/goods/"+this.goodsId).then(
              function(response){
                goodsTitle = response.title;
              },
              function(){
                vux.alert.show({
                  title: '',
                  content: '',
                  onHide () {
                      window.location.reload();
                  }
                });
              }
            );

            var userPhoneNumber;
            httpClient.get("/user/"+this.userId).then(
              function(response){
                userPhoneNumber = response.phoneNumber;
              },
              function(){
                vux.alert.show({
                  title: '',
                  content: '',
                  onHide () {
                      window.location.reload();
                  }
                });
              }
            );

            order.fields = [
              {
                label: '',
                value: this.displayId
              },
              {
                label: '',
                value: userPhoneNumber
              }, {
                label: '',
                value: goodsTitle
              }
            ];

            order.price = "" + this.price + ".00";

            orderList.push(order);
          })
        }else{
          vux.alert.show({
            title: '',
            content: '',
            onHide () {
                window.history.back();
            }
          });
        }
      },
      function(){
        vux.alert.show({
          title: '',
          content: '',
          onHide () {
              window.history.back();
          }
        });

      }
    )
  },
  methods:{
    getButton(order){
      var button = {
        style: 'default',
        text:"",
        onButtonClick: function() {
        }
      }

      var httpClient = this.$http;
      var vux = this.$vux;
      if("WAIT_FOR_PAY" == order.status){
        button.style = "primary";
        button.text = "";

        button.onButtonClick = function () {
          httpClient.post('/orders/action/userPayed/orderId/'+order.id,{}).then(
            function(response){
               if("0" == response.resultCode){
                 vux.alert.show({
                   title: '',
                   content: '',
                   onHide () {
                      window.location.reload();
                   }
                 });
               }else{
                 vux.alert.show({
                   title: '',
                   content: ''
                 });
               }
            },
            function(){
               vux.alert.show({
                 title: '',
                 content: ''
               });
            }
          )
        }
      }
      if("PAY_SUCCESS" == order.status){
        button.style = "primary";
        button.text = "";
        button.onButtonClick =function() {
          button.onButtonClick = function () {
            httpClient.post('/orders/action/deliverGoods/orderId/'+order.id,{}).then(
              function(response){
                 if("0" == response.resultCode){
                   vux.alert.show({
                     title: '',
                     content: '',
                     onHide () {
                        window.location.reload();
                     }
                   });
                 }else{
                   vux.alert.show({
                     title: '',
                     content: ''
                   });
                 }
              },
              function(){
                 vux.alert.show({
                   title: '',
                   content: ''
                 });
              }
            )
          }
        }
      }
      if("DILIVERED" == order.status){
        button.style = "primary";
        button.text = "";
        button.onButtonClick =function() {
          button.onButtonClick = function () {
            httpClient.post('/orders/action/receiveGoods/orderId/'+order.id,{}).then(
              function(response){
                 if("0" == response.resultCode){
                   vux.alert.show({
                     title: '',
                     content: '',
                     onHide () {
                        window.location.reload();
                     }
                   });
                 }else{
                   vux.alert.show({
                     title: '',
                     content: ''
                   });
                 }
              },
              function(){
                 vux.alert.show({
                   title: '',
                   content: ''
                 });
              }
            )
          }
        }
      }
      return [button]
    }
  }
}



